@use 'global' as *;

.pagination {

}

.pagination__inner {
   width: 100%;
   margin: 0 auto;
   padding: $padding-pc;
}


.page-numbers {
   display: flex;
   justify-content: center;
}

.page-numbers li {
   margin-left: rem(20);
   border: rem(2) solid $black;
   padding: rem(8) rem(16);
}

.page-numbers li:hover {
   background-color: $accent;
}

